@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&family=Play:wght@700&family=Rubik+Dirt&display=swap');

$shadow: #e5ecf9 0 5px 20px 0;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

a {
  text-decoration: none;
}

body {
  background: #ffebff;
}

header {
  box-shadow: #e5ecf9 0 5px 20px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  margin: 0 auto;
  border-bottom: 1px solid #e5ecf9;
  background-color: #ffffffbf;
  -webkit-box-shadow: #e5ecf9 0 5px 20px 0;
  z-index: 99;
  backdrop-filter: blur(2px);

  .container {
    height: 100%;
  }

  .header__container {
    display: flex;
    justify-content: space-between;
    height: 100%;
    align-items: center;
  }
}

.link {
  color: #cb1fc5;
}

.footer {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px 0;
  }

  &__menu {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__link {
    font-size: 18px;
    color: #41004c;
    text-decoration: none;
  }

  &__left {
    width: 40%;
    padding-right: 10%;
  }

  &__text {
    margin: 20px 0;
    font-size: 14px;
    font-weight: 500;
  }

  &__socials {
    display: flex;
    gap: 10px;
    align-items: center;
  }

  &__social {
    color: #410848;
    font-size: 24px;
  }
}

.normalizer {
  height: 120px;
}

.menu {
  display: flex;
  gap: 20px;

  &__link {
    font-size: 18px;
    color: #41004c;
    text-decoration: none;
  }
}

#block {
  width: 88px;
  position: fixed;
  height: 100px;
  color: #FFF;
  background-image: url(http://wp-riva-site/wp-content/uploads/2023/07/pngitem_1377229.png);
  background-size: cover;
  background-position: center;
  z-index: 100;
  cursor: pointer;
}

.loader {
  z-index: 200;
  background: #0c0015;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  gap: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &.hide {
    display: none;
  }

  &__text {
    color: white;
    font-size: 18px;
    text-align: center;
  }

  &__img {
    width: 100px;
    height: 100px;
    text-align: center;

    img {
      max-width: 100%;
      max-height: 100%;
    }
  }

  &__btn {
    color: white;
    background: #7906c0;
    padding: 10px 20px;
    border-radius: 5px;
    outline: none;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;

    &:disabled {
      opacity: .7;
      cursor: none;
    }
  }
}

body {
  overflow-x: hidden;
}

.logo {
  font-family: 'Rubik Dirt', sans-serif;
  font-size: 28px;
  color: #490052;
  text-transform: uppercase;
}

.search {
  display: flex;
  gap: 10px;
  background: white;
  margin-bottom: 20px;
  align-items: center;

  &__icon {
    font-size: 22px;
    color: #9d9d9d;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 10px;
  }

  &__input {
    border: none;
    outline: none;
    width: 100%;
    height: 100%;
    padding: 12px 10px;
  }
}

.post {
  border-radius: 15px;
  background-color: #fff;
  -webkit-box-shadow: $shadow;
  box-shadow: $shadow;
  -webkit-transition: -webkit-box-shadow .4s;
  transition: box-shadow .4s,-webkit-box-shadow .4s;
  overflow: hidden;
  cursor: pointer;
  padding: 30px 40px 40px 40px;
  margin-bottom: 40px;
  display: block;

  &__category {
    color: #c95cc0;
    font-size: 14px;
  }

  &__title {
    font-size: 22px;
    font-weight: 600;
    line-height: 38px;
    margin-bottom: 13px;
    color: #490052;
  }

  &__description {
    max-height: 90px;
    margin-bottom: 15px;
    text-overflow: ellipsis;
    font-weight: 500;
    overflow: hidden;
    color: #a482ad;
  }

  &__date {
    font-size: 14px;
    font-weight: 300;
    color: #9b9b9b;
  }
}

.wrapper {
  width: 100%;
  max-width: 650px;
  margin: 0 auto;
}

.category {
  background: white;
  color: #57006c;
  padding: 10px 15px;
  border-radius: 10px;
  cursor: pointer;

  &:hover {
    background: #ebdeff;
  }

  &.active {
    background: #e0c5ff;
  }
}

.goTop {
  position: fixed;
  font-size: 2.2rem;
  z-index: 1;
  color: #eb9aff;
  width: 60px;
  height: 60px;
  border: none;
  border-radius: 100%;
  cursor: pointer;
  right: 50px;
  bottom: 50px;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.categories {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 40px;
}

.main-container {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  height: auto;
}

.post {
  p {
    color: #6e5a78;
  }

  &-title {
    font-size: 35px;
    margin-bottom: 40px;
    font-weight: 700;
  }
}